.split {
  height: 100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
  border: 5px solid black;

}

* {
  font-family: Arial;
}

.left {
  left: 0;
  background-color: red;
}


.right {
  right: 0;
  background-color: white;
}


.centered {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.circle-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-top: 325px;
  margin-right: 12px;

}

.circleO {
  width: 50px;
  height: 50px;
  background-color: white;
  border: 5px solid black;
  border-radius: 50%;
  z-index: 5;
}

.pokemon-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.pokemon-item {
  border: 1px solid #000000;
  background-color: aliceblue;
  padding: 10px;
  cursor: pointer;
  border-radius: 25px;
}

.pokemon-item img {
  max-width: 100%;
}